.footer{
    border-top: 2px solid var(--tertiary-color);
    margin-top: 5vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-width: 80vw;
    align-items: center;
}

.mainMenu{
    display: flex;
    flex-direction: row;
    padding-left: 0;
    @media (max-width: 480px){
        min-width: 80vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

}
.mainMenuItems{
    list-style: none;
    text-align: center;
    padding: 1.6vh 1.5vw;
    &:first-child{
    }
    &:last-child{
        border-right: none;
    }
}

.link{
    text-decoration: none;
    color: var(--primary-color);
    
    &:active{
        color: var(--link-color);
    };
    &:hover{
        color: var(--link-color);
    }
}
.footerParagraph{
    @media (max-width: 480px){
        display: none
    }
}