.socials{
    display: flex;
    flex-direction: row;
}
ul{
    display: flex;
    flex-direction: row;
    padding: 0;
}

.listIcons{
    list-style: none;
    margin-right: 2vw;
    width: 35px;
    height: 35px;
    &:last-child{
        margin-right: 0;
    }
}
.listImage{
    transform: rotate(0deg);
    min-width: 35px;
    width: 35px;
    height: 35px;
    
}
.listImage:hover{
    transform: rotate(15deg);
    animation-duration: 3s;
}
.listIcons:hover{
    background-color: rgb(221, 84, 255);
    border-radius: 5px;
}